@import url(https://fonts.googleapis.com/css2?family=Monoton&display=swap);
@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:Arial, Helvetica, sans-serif;margin:0;padding:0;-webkit-user-select:none;-ms-user-select:none;user-select:none;background:#de6161;background:linear-gradient(to right, #2657eb, #de6161);background-attachment:fixed;background-size:cover;background-position:center}#root{display:grid;margin:auto;place-items:center;height:100vh;background-color:rgba(200,200,200,0.4);-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px)}.App{--border-width: 5px;border-radius:var(--border-width);position:relative;font-size:1.4rem;padding:5px 20px 70px;display:grid;grid-template-columns:[first] 70px [pad-start] auto [controller-start] 100px [end];grid-template-rows:[header-start] 50px [line-one] 80px [pad-start] auto [pad-end];background:#222;color:white}.App::after{position:absolute;content:"";top:calc(-1 * var(--border-width));left:calc(-1 * var(--border-width));z-index:-1;width:calc(100% + var(--border-width) * 2);height:calc(100% + var(--border-width) * 2);background:linear-gradient(60deg, #5f86f2, #a65ff2, #f25fd0, #f25f61, #f2cb5f, #abf25f, #5ff281, #5ff2f0);background-size:300% 300%;background-position:0 50%;border-radius:calc(2 * var(--border-width));-webkit-animation:moveGradient 4s alternate infinite;animation:moveGradient 4s alternate infinite}@-webkit-keyframes moveGradient{50%{background-position:100% 50%}}@keyframes moveGradient{50%{background-position:100% 50%}}.Logo{grid-column:first / end;font-family:'Press Start 2P', cursive;font-size:2rem;margin-top:1rem}.Logo--rainbow-text{background-image:linear-gradient(60deg, #5f86f2, #a65ff2, #f25fd0, #f25f61, #f2cb5f, #abf25f, #5ff281, #5ff2f0);-webkit-background-clip:text;background-clip:text;color:transparent}.Controls{font-size:2rem;grid-column:controller-start / end;grid-row:pad-start / pad-end;display:-webkit-flex;display:flex;-webkit-flex-flow:column;flex-flow:column;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.Controls__power{cursor:pointer}.Controls__volume{display:-webkit-flex;display:flex;-webkit-flex-flow:column;flex-flow:column;-webkit-align-items:center;align-items:center}.Controls2{grid-column:first / controller-start;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.Controls2>*{margin:0 1rem}.Controls2__bank{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.Controls2__bank span{padding-right:1rem}.Controls2__switch{position:relative;display:inline-block;width:60px;height:34px;box-shadow:inset 1px 1px 1px #000000, inset 0px 0px 1px #0d0d0d;border:0.2px solid #010101;border-radius:2.6px}.Controls2__switch input{display:none}.Controls2__switch input:focus+.Controls2__switch__slider{box-shadow:0 0 1px #2196f3}.Controls2__switch input:checked+.Controls2__switch__slider:before{-webkit-transform:translateX(-26px);transform:translateX(-26px)}.Controls2__switch input:checked+.Controls2__switch__slider:after{content:"II";left:75%}.Controls2__switch__slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#ccc}.Controls2__switch__slider:before{position:absolute;content:"";height:26px;width:26px;left:30px;bottom:4px;background-color:white}.Controls2__switch__slider:after{content:"I";color:black;font-weight:bold;position:absolute;top:50%;left:25%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.Controls2__display{background-color:#cccccc;box-shadow:inset 1px 1px 1px #000000, inset 0px 0px 1px #0d0d0d;border:0.2px solid #010101;border-radius:2.6px;color:black;width:200px;height:34px;font-family:"VT323", monospace;font-weight:bold;font-size:1.8rem;display:grid;place-items:center}.PadBank{display:grid;grid-column:pad-start / controller-start;grid-row:pad-start / pad-end;grid-gap:15px;gap:15px;grid-template-columns:repeat(3, 100px);grid-template-rows:repeat(3, 100px)}.DrumPad{width:100px;height:100px;display:grid;place-items:center;border-radius:5px;font-size:2rem;font-weight:bold;cursor:pointer;color:black;background-color:white;box-shadow:inset 0 -0.5em 0 -0.35em rgba(0,0,0,0.17);z-index:2}input[type="range"]{-webkit-appearance:none;margin:18px 0;width:200px;-webkit-transform:rotate(270deg);transform:rotate(270deg);position:relative;bottom:90px}input[type="range"]:focus{outline:none}input[type="range"]::-webkit-slider-runnable-track{width:200px;height:8.4px;cursor:pointer;box-shadow:1px 1px 1px #000000, 0px 0px 1px #0d0d0d;background:#ccc;border-radius:1.3px;border:0.2px solid #010101}input[type="range"]::-webkit-slider-thumb{box-shadow:1px 1px 1px #000000, 0px 0px 1px #0d0d0d;border:1px solid #000000;height:36px;width:16px;border-radius:3px;background:#fff;cursor:pointer;-webkit-appearance:none;margin-top:-14px}input[type="range"]:focus::-webkit-slider-runnable-track{background:#ccc}input[type="range"]::-moz-range-track{width:200px;height:8.4px;cursor:pointer;box-shadow:1px 1px 1px #000000, 0px 0px 1px #0d0d0d;background:#ccc;border-radius:1.3px;border:0.2px solid #010101}input[type="range"]::-moz-range-thumb{box-shadow:1px 1px 1px #000000, 0px 0px 1px #0d0d0d;border:1px solid #000000;height:36px;width:16px;border-radius:3px;background:#fff;cursor:pointer}input[type="range"]::-ms-track{width:200px;height:8.4px;cursor:pointer;background:transparent;border-color:transparent;border-width:16px 0;color:transparent}input[type="range"]::-ms-fill-lower{background:#2a6495;border:0.2px solid #010101;border-radius:2.6px;box-shadow:1px 1px 1px #000000, 0px 0px 1px #0d0d0d}input[type="range"]::-ms-fill-upper{background:#ccc;border:0.2px solid #010101;border-radius:2.6px;box-shadow:1px 1px 1px #000000, 0px 0px 1px #0d0d0d}input[type="range"]::-ms-thumb{box-shadow:1px 1px 1px #000000, 0px 0px 1px #0d0d0d;border:1px solid #000000;height:36px;width:16px;border-radius:3px;background:#fff;cursor:pointer}input[type="range"]:focus::-ms-fill-lower{background:#ccc}input[type="range"]:focus::-ms-fill-upper{background:#ccc}

